// All the Typography settings here
html {
    font-size: 62.5%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    font-family: $font;

    @include font-size(1.6rem);

    line-height: 1.4375em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
}

h1,
h2,
h3 {
    font-family: $font-title;
}

h1 {
    @include font-size(4.6rem);

    line-height: 1.2608em;
    margin: 0 0 20px;
}

h2 {
    font-family: $font-title;
    font-size: 50px;
    font-weight: 400;
    line-height: 51px;
    margin: 0 0 25px;
}

h3 {
    font-size: 35px;
    line-height: 42px;
    font-weight: 400;
    margin: 0 0 9px;
}

h4 {
    font-family: $font;
    font-size: 33px;
    font-weight: 500;
    line-height: 42px;
    margin: 0 0 9px;
}

h5 {
    font-size: 28px;
    line-height: 40px;
    font-weight: 700;
}

h6 {
    font-size: 21px;
    line-height: 29px;
    font-weight: 300;
}

p {
    font-size: 18px;
    line-height: 27px;
    margin: 0 0 25px;

    strong {
        font-weight: 500;
    }
}

strong,
b {
    font-weight: 700;
}

ul {
    list-style: none;
    margin: 0;
    margin-bottom: 35px;
    padding: 0;

    li {
        position: relative;
        padding-left: 12px;
        font-size: 18px;
        line-height: 27px;
        margin-bottom: 3px;
    }
}

a {
    color: inherit;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

.font-weight-light {
    font-weight: 300;
}

.list {
    li {
        &::before {
            content: "";
            position: absolute;
            top: 11px;
            left: 0;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: $color-black;
        }
    }
}
